@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=DM+Sans&display=swap");
html {
  display: flex;
  justify-content: center;
}

body {
  font-family: DM Sans, sans-serif;
  max-width: 1440px;
  width: 100%;
}
body button {
  background-color: transparent;
  border: 0;
  color: #901c1c;
  cursor: pointer;
  padding: 0;
}

.btContact {
  background-color: #901c1c;
  color: white;
  font-weight: bold;
  border-radius: 5px;
}
.btContact-mobile {
  width: 10.625rem;
  height: 2.1875rem;
  font-size: 1.125rem;
  position: fixed;
  bottom: 1.875rem;
  left: 50vw;
  transform: translateX(-50%);
}
@media screen and (min-width: 992px) {
  .btContact-mobile {
    display: none;
  }
}
.btContact-desktop {
  display: none;
}
@media screen and (min-width: 992px) {
  .btContact-desktop {
    position: absolute;
    left: 28.125rem;
    top: 3.9375rem;
    width: 10.625rem;
    height: 4.3125rem;
    font-size: 1.25rem;
    display: block;
  }
}

.photographerLink {
  width: 12.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.photographerLink .imgContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 12.5rem;
  height: 12.5rem;
  margin-bottom: 1.25rem;
  border-radius: 50%;
  overflow: hidden;
  box-shadow: 0px 5px 15px 5px rgba(0, 0, 0, 0.2);
}
.photographerLink .imgContainer img {
  position: relative;
  max-width: none;
}
.photographerLink .imgContainer .mimi {
  width: 19rem;
  top: 1.0625rem;
}
.photographerLink .imgContainer .ellie {
  width: 13.5625rem;
  left: 0.1875rem;
  top: 0.25rem;
}
.photographerLink .imgContainer .tracy {
  width: 32.375rem;
  top: 1.6875rem;
}
.photographerLink .imgContainer .nabeel {
  width: 32.875rem;
  top: 1.8125rem;
}
.photographerLink .imgContainer .rhode {
  width: 33.1875rem;
  top: 13.1875rem;
  left: 2.25rem;
}
.photographerLink .imgContainer .marcel {
  width: 33.75rem;
  top: 1.75rem;
}
.photographerLink h2 {
  color: #d3573c;
  font-size: 2.25rem;
  white-space: nowrap;
}

.filterLink {
  border: 0.5px solid #c4c4c4;
  border-radius: 0.6875rem;
  margin: 0 0.125rem;
  padding: 0.125rem 0.375rem;
}
.filterLink:focus, .filterLink:hover {
  background-color: #901c1c;
  color: #fff;
}
@media screen and (min-width: 992px) {
  .filterLink {
    margin: 0.125rem;
  }
}

.logo {
  align-items: center;
  display: flex;
  grid-area: logo;
  height: 109px;
}
@media screen and (min-width: 992px) {
  .logo {
    height: 137px;
  }
  .logo img {
    fill: none;
    height: 50px;
    width: 200px;
  }
}

.photographerArticle {
  flex: 1 0 25%;
  display: flex;
  flex-direction: column;
  width: 25%;
  min-width: 100%;
  align-items: unset;
  height: 9.4rem;
  margin: 0;
  position: relative;
  bottom: 6px;
}
@media screen and (min-width: 992px) {
  .photographerArticle {
    background-color: #fafafa;
    height: auto;
    border-radius: 0.3125rem;
  }
}
.photographerArticle .imgNameContainer {
  display: flex;
  position: relative;
  bottom: 0.625rem;
  flex-direction: row-reverse;
  align-items: flex-start;
  width: 100%;
  justify-content: space-between;
  margin: 0;
  padding: 0;
}
.photographerArticle .imgNameContainer .imgContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 1.25rem;
  border-radius: 50%;
  overflow: hidden;
  box-shadow: 0px 5px 15px 5px rgba(0, 0, 0, 0.2);
  width: 6.25rem;
  height: 6.25rem;
}
@media screen and (min-width: 992px) {
  .photographerArticle .imgNameContainer .imgContainer {
    width: 12.5rem;
    height: 12.5rem;
    position: relative;
    top: 4rem;
    right: 3.47%;
  }
}
.photographerArticle .imgNameContainer .imgContainer img {
  position: relative;
  max-width: none;
}
.photographerArticle .imgNameContainer .imgContainer .mimi {
  top: 0.5rem;
  width: 10.625rem;
}
@media screen and (min-width: 992px) {
  .photographerArticle .imgNameContainer .imgContainer .mimi {
    width: 21rem;
    top: 1.0625rem;
  }
}
.photographerArticle .imgNameContainer h2 {
  color: #d3573c;
  font-size: 2.6rem;
  position: relative;
  top: 0.625rem;
  white-space: nowrap;
  height: 47px;
}
@media screen and (min-width: 992px) {
  .photographerArticle .imgNameContainer h2 {
    font-size: 64px;
    margin-top: 56px;
    margin-left: 3.47%;
    height: 83px;
  }
}
.photographerArticle p {
  margin: 1.5px 0;
}
.photographerArticle .location,
.photographerArticle .tagline,
.photographerArticle .photographerNav {
  position: relative;
  bottom: 4.6875rem;
  margin-bottom: 0.25rem;
  font-size: 0.8125rem;
}
@media screen and (min-width: 992px) {
  .photographerArticle .location,
.photographerArticle .tagline,
.photographerArticle .photographerNav {
    margin-left: 3.47%;
    position: unset;
  }
}
.photographerArticle .location {
  color: #901c1c;
  line-height: 0.9375rem;
}
@media screen and (min-width: 992px) {
  .photographerArticle .location {
    font-size: 1.5rem;
    margin-bottom: 1.1875rem;
    height: 1.9375rem;
  }
}
.photographerArticle .tagline {
  color: #757575;
  margin-bottom: 0.4375rem;
}
@media screen and (min-width: 992px) {
  .photographerArticle .tagline {
    font-size: 1.125rem;
    margin-bottom: 0.9375rem;
  }
}
.photographerArticle .price {
  display: none;
}
@media screen and (min-width: 992px) {
  .photographerArticle .photographerNav {
    margin-bottom: 3.25rem;
  }
}
@media screen and (min-width: 992px) {
  .photographerArticle .photographerNav .filterLink {
    font-size: 1.125rem;
    font-weight: bold;
  }
}

.photographerSection {
  flex: 1 0 25%;
  display: flex;
  flex-direction: column;
  min-width: 20.9375rem;
  width: 20.9375rem;
  max-width: 20.9375rem;
  margin: 5px;
}
.photographerSection .imgContainer {
  height: 11.9375rem;
  width: 100%;
  overflow: hidden;
  border-radius: 5px;
}
.photographerSection .imgContainer img {
  position: relative;
}
.photographerSection .imgContainer .animalRainbow {
  bottom: 2.9375rem;
}
.photographerSection .infoContainer {
  display: flex;
  justify-content: space-between;
  margin-top: 0.625rem;
  align-items: center;
}
.photographerSection .infoContainer p {
  color: #901c1c;
}
.photographerSection .infoContainer .imgTitle,
.photographerSection .infoContainer .like {
  font-size: 0.875rem;
}
.photographerSection .infoContainer .price {
  font-size: 0.75rem;
  position: relative;
  left: 5.5625rem;
}
.photographerSection .infoContainer .like::after {
  content: "\f004";
  font-family: "Font Awesome 5 Free";
  font-weight: bold;
  margin-left: 0.3125rem;
  font-size: 0.6875rem;
}

body {
  margin: 0 100px;
}

header {
  margin: 0 20px;
  padding-top: 3px;
}
@media screen and (min-width: 992px) {
  header {
    margin: 0 0 20px 0;
  }
}

label {
  font-size: 18px;
  font-weight: bold;
  cursor: pointer;
}

#widget {
  display: flex;
  align-items: center;
}

.widget select,
.no-widget .select {
  /* Ce sélecteur CSS dit fondamentalement :
      - soit la classe body est "widget" et donc l'élément <select> réel sera caché
      - soit la classe body n'a pas changé, elle est toujours "no-widget",
        et donc les éléments, dont la classe est « select », doivent être cachés */
  position: absolute;
  left: -5000em;
  height: 0;
  overflow: hidden;
}

.select {
  /* Celui-ci crée un contexte de positionnement pour la liste des options */
  position: relative;
  /* Celui-ci fait que le widget devient partie du flot textuel
      et devient en même temps dimensionnable */
  display: flex;
  align-items: center;
  /* Toutes les tailles seront exprimées en valeurs em (lettre M, étalon
      du cadratin : cadre dans lequel sont dessinées toutes les lettres d'une
      police de caractères) pour des raisons d'accessibilité (pour être sûrs
      que le widget reste redimensionnable si l'utilisateur utilise le zoom
      du navigateur en mode texte exclusif). Les calculs sont faits en
      supposant que 1em==16px qui est la valeur par défaut dans la majorité
      des navigateurs. Si vous êtes perdus avec les conversions entre px et
      em, essayez http://riddle.pl/emcalc/ */
  background-color: #901c1c;
  color: white;
  width: 170px;
  height: 60px;
  border-radius: 5px;
  margin-left: 25px;
  /* Le sélecteur .select ici est du sucre syntaxique (le fait de donner au
    programmeur des possibilités d'écriture plus succinctes ou plus proches
    d'une notation usuelle) pour s'assurer que les classes que nous définissons
    sont les seules à l'intérieur du widget. */
}
.select.active {
  outline: none;
  /* Cette propriété box-shadow n'est pas requise à proprement parler, mais il est
  	 important de s'assurer que l'état actif soit visible, c'est pourquoi nous
  	 utilisons une valeur par défaut. Vous êtes libre de la modifier. */
  border-radius: 5px 5px 0 0;
}
.select.active:after {
  transform: rotate(180deg);
}
.select:after {
  font-family: "Font Awesome 5 Free";
  content: "\f078";
  font-weight: bold;
  z-index: 5;
  /* Il est important d'empêcher la flèche de chevaucher la liste des options */
  margin-right: 18px;
  font-size: 19px;
  transform: rotate(0);
}
.select .highlight {
  background: #d3573c;
  color: #ffffff;
  border-radius: 5px;
}
.select .value {
  /* Comme la valeur peut être plus large que le widget, nous devons nous
     assurer qu'elle ne changera pas la largeur du widget. */
  display: inline-block;
  width: 100%;
  overflow: hidden;
  /* Et si le contenu déborde, c'est mieux d'avoir une jolie abreviation. */
  white-space: nowrap;
  text-overflow: ellipsis;
  font-size: 18px;
  font-weight: bold;
  margin-left: 20px;
}
.select .optList {
  /* Ceci assure que la liste des options sera affichée au dessous de la valeur
  	 et en dehors du flot HTML */
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  /* Nous disons explicitement que la liste des options doit toujours passer sur la flèche */
  /* cela réinitialiser le style par défaut de l'élément ul */
  list-style: none;
  margin: 0;
  padding: 0;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  /* Cela nous assure que même si les valeurs sont plus petites que le widget,
     la liste des options sera aussi large que le widget lui‑même */
  min-width: 100%;
  /* Dans le cas où la liste est trop longue, son contenu débordera verticalement
     (ce qui ajoutera une barre de déroulement automatiquement) mais jamais horizontalement
     (car nous n'avons jamais défini de largeur, la liste ajustera automatiquement sa largeur
     Si ce n'est pas possible, le contenu sera tronqué) */
  max-height: 10em;
  /* 100px */
  overflow-y: hidden;
  overflow-x: hidden;
  border-top-width: 0.1em;
  /* 1px */
  border-radius: 0.4em 0.4em 0.4em 0.4em;
  /* 0 0 4px 4px */
  box-shadow: 0 0.2em 0.4em rgba(0, 0, 0, 0.4);
  /* 0 2px 4px */
  background: #901c1c;
  height: 170px;
  font-size: 18px;
}
.select .optList.hidden {
  /* Ceci est un moyen simple pour cacher la liste tout en conservant l'accessibilité,
    nous reparlerons plus loin d'accessibilité */
  max-height: 0;
  visibility: hidden;
}
.select .optList .option {
  display: flex;
  align-items: center;
  margin: 0 10px;
  padding-left: 10px;
  height: 33.33%;
  font-weight: bold;
  border-top: 1px solid white;
}
.select .optList .borderOff {
  border: none;
}

.photoArticle {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}