.photographerArticle {
	flex: 1 0 25%;
	display: flex;
	flex-direction: column;
	width: 25%;
	min-width: 100%;
	align-items: unset;
	height: 9.4rem;
	margin: 0;
	position: relative;
	bottom: 6px;
	@include desktop {
		background-color: $secondary3;
		height: auto;
		border-radius: 0.3125rem;
	}
	.imgNameContainer {
		display: flex;
		position: relative;
		bottom: 0.625rem;
		flex-direction: row-reverse;
		align-items: flex-start;
		width: 100%;
		justify-content: space-between;
		margin: 0;
		padding: 0;
		.imgContainer {
			display: flex;
			align-items: center;
			justify-content: center;
			margin-bottom: 1.25rem;
			border-radius: 50%;
			overflow: hidden;
			box-shadow: 0px 5px 15px 5px rgba(0, 0, 0, 0.2);
			width: 6.25rem;
			height: 6.25rem;
			@include desktop {
				width: 12.5rem;
				height: 12.5rem;
				position: relative;
				top: 4rem;
				right: 3.47%;
			}
			img {
				position: relative;
				max-width: none;
			}
			.mimi {
				top: 0.5rem;
				width: 10.625rem;
				@include desktop {
					width: 21rem;
					top: 1.0625rem;
				}
			}
		}
		h2 {
			color: $primary2;
			font-size: 2.6rem;
			position: relative;
			top: 0.625rem;
			white-space: nowrap;
			height: 47px;
			@include desktop {
				font-size: 64px;
				margin-top: 56px;
				margin-left: 3.47%;
				height: 83px;
			}
		}
	}
	p {
		margin: 1.5px 0;
	}
	.location,
	.tagline,
	.photographerNav {
		position: relative;
		bottom: 4.6875rem;
		margin-bottom: 0.25rem;
		font-size: 0.8125rem;
		@include desktop {
			margin-left: 3.47%;
			position: unset;
		}
	}
	.location {
		color: $primary1;
		line-height: 0.9375rem;
		@include desktop {
			font-size: 1.5rem;
			margin-bottom: 1.1875rem;
			height: 1.9375rem;
		}
	}
	.tagline {
		color: #757575;
		margin-bottom: 0.4375rem;
		@include desktop {
			font-size: 1.125rem;
			margin-bottom: 0.9375rem;
		}
	}
	.price {
		display: none;
	}
	.photographerNav {
		@include desktop {
			margin-bottom: 3.25rem;
		}
		.filterLink {
			@include desktop {
				font-size: 1.125rem;
				font-weight: bold;
			}
		}
	}
}
